.header {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	z-index: 1;
	margin: 0 auto;
	padding: 65px 15px;
	box-sizing: border-box;
	max-width: 85%;
	width: 100%;

	&-logo {
		font-family: $avenir;
		font-size: 34px;
		color: $white;
		transition: 0.25s;
		&:hover {
			text-decoration: none; }

		&__img {
			display: flex;
			align-items: center;
			margin-left: 20px;
			max-width: 225px;
			img {
				width: auto;
				height: auto; } } }

	&-nav {
		&-ul {
			margin: auto 0;
			padding: 0;
			list-style: none;

			&-li {
				margin-left: 15px; } } }


	&-user-block {
		margin-left: 100px;
		.btn_transparent {
			margin-left: 15px; } }


	&-link {
		display: block;
		box-sizing: border-box;
		padding: 5px 15px;
		font-family: $avenir;
		font-size: 22px;
		font-weight: 600;
		color: $white;
		transition: .25s;
		&:hover {
			text-decoration: none; } }

	.btn-mobile-menu {
		display: none; } }


@media (max-width: 1700px) {
	.header {
		max-width: 90%;
		&-nav-ul-li {
			margin-left: 10px; }
		&-user-block {
			margin-left: 60px; } } }


@media (max-width: 1500px) {
	.header {
		max-width: 95%;
		&-logo {
			font-size: 28px;
			&__img {
				max-width: 180px; } }
		&-user-block {
			.btn_transparent {
				margin-left: 10px;
				font-size: 18px; } }
		&-link {
			font-size: 20px; } } }


@media (max-width: 1400px) {
	.header {
		padding: 35px 15px;
		max-width: 100%;
		&-nav-ul-li {
			margin-left: 5px; }
		&-user-block {
			margin-left: 40px;
			.btn_transparent {
				margin-left: 5px; } } } }

@include media-breakpoint-down(lg) {
	.header {
		padding: 5px 15px;
		&-logo {
			font-size: 24px; }
		&-nav-ul-li {
			margin-left: 0; }
		&-user-block {
			margin-left: 20px; }
		&-link {
			padding: 5px 10px;
			font-size: 18px; } } }

@include media-breakpoint-down(md) {
	.header {
		&-logo {
			position: relative;
			z-index: 2;
			font-size: 22px; }

		&-links-js {
			position: fixed;
			top: 0;
			left: 0;
			right: 0;
			z-index: 1;
			padding: 50px 15px 30px;
			box-sizing: border-box;
			height: 100vh;
			background: $blue-sky url('../img/bg-main.png') top center no-repeat;
			background-size: 100% 200%;
			transition: .25s;
			transform: translate(0, -100%);
			overflow: auto;
			&_show {
				transform: translate(0, 0); } }
		&-link {
			margin-bottom: 15px; }

		.btn-mobile-menu {
			position: relative;
			z-index: 2;
			display: flex;
			align-items: center;
			font-family: $avenir;
			font-size: 22px;
			color: $white;
			background-color: transparent;
			border: 0;
			outline: none;
			.lines {
				margin-left: 10px;
				width: 25px; }

			&_active {
				.lines {
					background-color: transparent;
					box-shadow:  0 0 0 transparent;
					&::before,
					&::after {
						background-color: $white; } } } } } }
